.blog {
    display: flex;
    flex-wrap: wrap;
    max-width: 1100px;
}

.blog-post-card {
    background-color: #f8f8f8;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    padding: 25px 50px;
    margin: 25px 50px;
    min-width: 300px;
}

.blog img {
    max-width: 500px;
    align-items: center;
}

.blog-post {
    max-width: 1000px;
    padding: 50px;
}